import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

const Button = props => <button>{props.children}</button>;

export default styled (Button)`
  display: flex;
  min-width: 150px;
  width: auto;
  max-width: 250px;
  height: 40px;
`;

Button.PropTypes = {
  children: PropTypes.string,
};

Button.defaultProps = {};
